/* Animações básicas */
.hover-scale {
  transition: all 0.2s ease-in-out;
}

.hover-scale:hover {
  transform: scale(1.02);
}

@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.animate-fade-in-up {
  animation: fadeInUp 0.5s ease-out;
}

/* Tamanhos de fonte */
.font-size-small {
  font-size: 14px;
}

.font-size-normal {
  font-size: 16px;
}

.font-size-large {
  font-size: 18px;
}

.font-size-xlarge {
  font-size: 20px;
}

/* Densidades */
.density-compact .p-4 {
  padding: 0.5rem;
}

.density-compact .p-6 {
  padding: 1rem;
}

.density-comfortable .p-4 {
  padding: 1.5rem;
}

.density-comfortable .p-6 {
  padding: 2rem;
}

/* Sem animações */
.no-animations * {
  animation: none !important;
  transition: none !important;
}
